import React from 'react';
import { useState, useEffect } from 'react';
import Authenticate from './Authenticate';
import Form from './Form';
import Footer from './Footer';
import firebase from 'firebase/app';
import 'firebase/auth';
import './App.css';

const firebaseConfig = {
  apiKey: "AIzaSyAOYtNKsCiwjosXZEsS7Zw-vyrABepxZXQ",
  authDomain: "prog-soc-site.firebaseapp.com",
  databaseURL: "https://prog-soc-site.firebaseio.com",
  projectId: "prog-soc-site",
  storageBucket: "prog-soc-site.appspot.com",
  messagingSenderId: "1050991347056",
  appId: "1:1050991347056:web:e0a9a55f015c36c1c3bbb5",
  measurementId: "G-MDS039HCQZ"
}

firebase.initializeApp(firebaseConfig);
const auth = firebase.auth();

function App() {
  const [isLoggedIn, setLog] = useState(false)
  const [email, setEmail] = useState('')
  const [password, setPass] = useState('')
  const [showError, setShowError] = useState(false)

  useEffect(() => {
    auth.onAuthStateChanged(user => {
      if (user) {
        setLog(true)
        setShowError(false)
      } else {
        setLog(false)
      }
    })
  }, [])

  const authUser = (email, password) => {
    auth.signInWithEmailAndPassword(email, password).catch(error => {
      setShowError(true)
      setTimeout(() => {
        setShowError(false)
      }, 2000)
    })
  }

  const emailHandler = email => {
    setEmail(email)
  }

  const passHandler = pass => {
    setPass(pass)
  }

  const logOut = () => {
    auth.signOut().catch(error => {
      const errorCode = error.code;
      const errorMessage = error.message;
      console.log(errorCode);
      console.log(errorMessage);
    })
    setEmail('')
    setPass('')
  }

  if (isLoggedIn) {
    return (
      <div>
        <Form logOut={logOut}/>
        <Footer/>
      </div>
    );
  } else {
    return (
      <div>
        <Authenticate authUser={authUser} emailHandler={emailHandler} 
        passHandler={passHandler} email={email} password={password}/> 
        <h3 className={showError ? 'show-error' : 'hide-error'} 
        style={{fontWeight: 'lighter', color: 'red', textAlign: 'center'}}>Incorrect credentials. Please check them again!</h3>
        <div style={{position: 'fixed', bottom: '0'}}><Footer/></div>
      </div>
    )
  } 
}

export default App;
