import React from 'react';
import './App.css';
import logo from './assets/ProgSocLogo.png';

function Authenticate({authUser, emailHandler, passHandler, email, password}) {
   
    const updateEmailInput = e => {
      e.preventDefault()
      emailHandler(e.target.value)
    }
  
    const updatePassInput = e => {
      e.preventDefault()
      passHandler(e.target.value)
    }
  
    const onSubmit = e => {
      e.preventDefault()
      authUser(email, password)
    }
  
    return (
        <div>
        <h1 style={{fontWeight: 'lighter', textAlign: 'center', marginBottom: '30px'}}><img src={logo} alt='progsoc-logo' width='100px' height='100px'/> Login</h1>
        <form onSubmit={onSubmit}>
            <div style={{marginRight: '48%'}}><h3 style={{fontWeight:'normal'}}>Email</h3></div>
            <div><input className='loginInput' name='emailInput' type='email' placeholder='Your email' onChange={updateEmailInput}></input></div>
            <div style={{marginRight: '45%', marginTop:'30px'}}><h3 style={{fontWeight:'normal'}}>Password</h3></div>
            <div><input className='loginInput' type='password' placeholder='Your password' onChange={updatePassInput}></input></div>
            <br/>
            <button className='coolButton' type='submit' style={{marginTop:'30px'}}>Log In</button>
        </form>
        </div>
    )
}

export default Authenticate;

