import React from 'react';
import firebase from 'firebase/app';
import 'firebase/firestore';
import logo from './assets/ProgSocLogo.png';
import { CirclePicker } from 'react-color';
import Tag from './Tag';
import './App.css';

const light = {
    fontWeight: 'lighter',
    textAlign: 'center'
}

class Form extends React.Component {
    constructor() {
        super();
        this.state = {
            description: '',
            location: '',
            name: '',
            photoUrl: '',
            startDate: new Date(),
            summary: '',
            selectValue: '',
            tagText: '',
            tagColor: '',
            tagNames: [],
            tagDBNames: [],
            tagColors: [],
            tagDBColors: [],
            showNewTag: false,
            firstTag: true,
            showError: false
        }
    }

    // fetch tag db

    fetchData = () => {
        const db = firebase.firestore();
        db.collection('tags').get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                this.setState(prevState => ({
                    tagDBNames: [...prevState.tagDBNames, doc.id],
                    tagDBColors: [...prevState.tagDBColors, doc.data()]
                }))
            })
        })
    }

    componentDidMount() {
        this.fetchData();
    }

    // various handlers for changing state after buttons are clicked

    updateInput = e => {
        this.setState({[e.target.name]: e.target.value})
    }

    pickColor = color => {
        this.setState({tagColor: color.hex})
    }

    // helper for addTag

    tagFlush = () => {
        this.setState({
            showNewTag: false,
            tagText: '',
            tagColor: '',
            showError: false
        })
    }

    noDups = () => {
        return !this.state.tagNames.includes(this.state.tagText.toLowerCase()) && !this.state.tagNames.includes(this.state.selectValue) 
        && !this.state.tagColors.includes(this.state.tagColor) && !this.state.tagDBNames.includes(this.state.tagText.toLowerCase()) && 
        !this.state.tagDBColors.includes(this.state.tagColor)
    }

    addTag = e => {
        e.preventDefault();
        if (this.state.selectValue !== '' && this.noDups()) {
            this.setState({firstTag: false})
            this.setState(prevState => ({
                tagNames: [...prevState.tagNames, this.state.selectValue],
                tagColors: [...prevState.tagColors, this.state.tagDBColors[this.state.tagDBNames.indexOf(this.state.selectValue)].color]
            }))
            this.tagFlush();
        } else {

            if (this.state.tagText === '' || this.state.tagColor === '' || !this.noDups())  {
                this.setState({showError: true, tagText: ''})
            } 
            else {
                this.setState({firstTag: false})
                this.setState(prevState => ({
                    tagNames: [...prevState.tagNames, this.state.tagText.toLowerCase()],
                    tagColors: [...prevState.tagColors, this.state.tagColor]
                }))
                this.tagFlush();
            }
        }    
    }

    removeTag = e => {
        e.preventDefault();

        if (this.state.tagNames.length > 0) {
            let tempNames = [...this.state.tagNames]
            let tempColors = [...this.state.tagColors]
            tempNames.pop()
            tempColors.pop()
            if (tempNames.length === 0) {
                this.setState({firstTag: true})
            }
            this.setState({tagNames: tempNames, tagColors: tempColors})
        } 
    }

    addNewTag = e => {
        e.preventDefault();
        this.setState({showNewTag: true, selectValue: ''})
    }

    hide = e => {
        e.preventDefault();
        this.setState({showNewTag: false})
    }

    renderExistingTags = () => {
        return this.state.tagDBNames.map((tagName, id) => <Tag name={tagName} color={this.state.tagDBColors[id].color} key={id}/>)
    }

    renderTags = () => {
        return this.state.tagNames.map((tagName, id) => <Tag name={tagName} color={this.state.tagColors[id]} key={id}/>)
    }

    getOptions = () => {
        return this.state.tagDBNames.map((tagName, id) => <option key={id} value={tagName}>{tagName.charAt(0).toUpperCase()+tagName.slice(1)}</option>)
    }

    addEvent = e => {
        e.preventDefault();
        const db = firebase.firestore();
        let sortedTagColors = []
        const savedTagNames = this.state.tagNames.slice()
        const sortedTagNames = this.state.tagNames.sort()
        let i;
        for (i=0;i<sortedTagNames.length;i++) {
            let oldTagIndex = savedTagNames.indexOf(sortedTagNames[i])
            sortedTagColors[i] = this.state.tagColors[oldTagIndex] 
        }
        db.collection('events').add({
            description: this.state.description,
            location: this.state.location,
            name: this.state.name,
            photo_url: this.state.photoUrl,
            start: firebase.firestore.Timestamp.fromDate(new Date(this.state.startDate)),
            summary: this.state.summary,
            tag_colors: sortedTagColors,
            tag_names: sortedTagNames
        });
        let j;
        for (j=0;j<this.state.tagNames.length;j++) {
            let tagName = this.state.tagNames[j]
            let tagColor = this.state.tagColors[j]
            if (!this.state.tagDBNames.includes(tagName)) {
                db.collection('tags').doc(tagName).set({color: tagColor})
            }
            
        }
        this.setState({
            description: '',
            location: '',
            name: '',
            photoUrl: '',
            startDate: new Date(),
            summary: '',
            selectValue: '',
            tagText: '',
            tagColor: '',
            tagNames: [],
            tagColors: [],
            showNewTag: false,
            firstTag: true,
            showError: false
        })
    }

    // renders the form

    render() {
        return (
            <div className='inputDiv'>
                <button className='coolButton' style={{float:'right', marginTop:'55px'}} onClick={this.props.logOut}>Log Out</button>
                <h1 style={light}> <img src={logo} alt='progsoc-logo' width='100px' height='100px'/> Event Creator</h1>
                <div>
                    <form onSubmit={this.addEvent}>
                    <div className='inputDiv'>
                            <label className='required' htmlFor='name'>Name</label><br/>
                            <input required name='name' type='text' 
                            onChange={this.updateInput} value={this.state.name}></input>
                        </div>
                        <div className='inputDiv'>
                            <label className='required' htmlFor='description'>Description</label><br/>
                            <textarea required name='description' type='text' rows='5'
                            onChange={this.updateInput} value={this.state.description}></textarea>
                        </div>
                        <div className='inputDiv'>
                            <label className='required' htmlFor='location'>Location</label><br/>
                            <input required name='location' type='text' 
                            onChange={this.updateInput} value={this.state.location}></input>
                        </div>
                        <div className='inputDiv'>
                            <label className='required' htmlFor='startDate'>Start Date</label><br/>
                            <input required name='startDate' type='datetime-local' 
                            onChange={this.updateInput} value={this.state.startDate}></input>
                        </div>
                        <div className='inputDiv'>
                            <label className='required' htmlFor='photoUrl'>Photo URL</label><br/>
                            <input required name='photoUrl' type='url' 
                            onChange={this.updateInput} value={this.state.photoUrl}></input>
                        </div>
                        <div className='inputDiv'>
                            <label htmlFor='summary'>Summary</label><br/>
                            <input name='summary' type='text' 
                            onChange={this.updateInput} value={this.state.summary}></input>
                        </div>
                        <div className='inputDiv'>
                            <label htmlFor='selectValue'>Tags</label><br/>
                            <select className='selectInput' name='selectValue' onChange={this.updateInput} value={this.state.selectValue}>
                                <option value=''>None</option>
                                {this.getOptions()}
                            </select>
                        </div>
                        {this.state.showNewTag ?  <div className='inputDiv'>
                            <label htmlFor='tagText'>New Tag</label><br/>
                            <input name='tagText' type='text' 
                            onChange={this.updateInput} value={this.state.tagText}></input>
                            </div> : null}
                        <div>
                            {this.state.showError ? <div>
                                <h3 style={{fontWeight:'300', color:'red'}}>Tags need a new name and colour!</h3> 
                                <h2 style={light}>Existing Tags</h2>
                                {this.renderExistingTags()}
                                </div> : null}
                            {!this.state.firstTag ? <div>
                                <h2 style={light}>Your Tags</h2>
                                {this.state.tagNames.length >= 0 ? this.renderTags() : null}
                            </div> : null}
                            {this.state.showNewTag ? <div>
                                <h2 style={light}>Select Tag Colour</h2> 
                                <div style={{display: "flex", justifyContent: "center", alignItems: "center", paddingTop: '5px', paddingBottom: '20px'}}>
                                <CirclePicker onChangeComplete={this.pickColor}/></div>
                                </div> : null}
                            <button className='tagButton coolButton' onClick={this.addTag}>Add Tag</button>
                            <button className='tagButton coolButton' onClick={this.removeTag}>Remove Last Tag</button>
                            {this.state.showNewTag ? <button className='tagButton coolButton' onClick={this.hide}>Hide</button> : 
                            <button className='tagButton coolButton' onClick={this.addNewTag}>Create New Tag</button>}
                        </div>
                        <button className='eventButton coolButton' type='submit'>Add Event</button>        
                    </form>
                </div>
            </div>
        )
    }   
}

export default Form;